<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <table class="table mt-4">
      <thead>
        <tr>
          <th style="width:140px;">購買時間</th>
          <th style="width:140px;">Email</th>
          <th>購買款項</th>
          <th style="width:140px;">應付金額</th>
          <th style="width:140px;">是否付款</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in orders" :key="item.id">
          <td>{{`${new Date(item.create_at*1000).getFullYear()}/${new Date(item.create_at*1000).getMonth() + 1}/${new Date(item.create_at*1000).getDate()}`}}</td>
          <td>{{item.user.email}}</td>
          <td>
            <div v-for="product in item.products" :key="product.id">
              {{`${product.product.title} 《數量：${product.qty}》`}}
              <br>
            </div>
          </td>
          <td class="text-right">{{item.total | currency}}</td>
          <td>
            <span v-if="item.is_paid" class="text-success">已付款</span>
            <span v-else>未付款</span>
          </td>
        </tr>
      </tbody>
    </table>

    <Pagination :page-data="pagination" v-on:refresh="getOrders"></Pagination>

  </div>
</template>

<script>

import Pagination from '../Pagination';

export default {
  components: { 
    Pagination 
  },
  data(){
      return {
          orders: [],
          pagination: {},
          isLoading: false,
      }
  },
  methods: {
      getOrders(page = 1){
          const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/orders?page=${page}`;
          const vm = this;
          vm.isLoading = true;
          vm.$http.get(api).then((response) => {
              vm.orders = response.data.orders;
              vm.pagination = response.data.pagination;
              vm.isLoading = false;
              console.log(response.data);
          });
      },
  },
  created() {
      this.getOrders();
  },
}
</script>

<style lang="scss" scoped>

@include md-width() {
  table {
    display: block; 
    overflow-x: auto; 
    white-space: nowrap;
  }  
}

</style>


