<template>
  <div>
    <div class="container">
      <div class="sidebar-title">
        <h4>分類</h4>
        <p>Category</p>
      </div>
      <ul class="navbar-nav">
        <li class="nav-item">
          <router-link class="nav-link" href="" to="/product-list">
            <img src="@/assets/images/icon_category_1.png" height="50px" alt="">
            <p>所有產品</p>
          </router-link>
        </li>
        <li class="nav-item" v-for="item in category" :key="item.title">
          <router-link class="nav-link" href="" :to="{ path: '/product-list', query: { category: item.title }}" >
            <img :src="item.img" height="50px" alt="">
            <p>{{item.title}}</p>
          </router-link>
        </li>
      </ul>  
    </div>
    

  </div>

  
</template>

<script>

import $ from 'jquery';

export default {
  data() {
    return{
      category: [
        {
          title: '時令蔬菜',
          img: require('@/assets/images/icon_category_2.png')
        },
        {
          title: '香甜水果',
          img: require('@/assets/images/icon_category_3.png')
        },
        {
          title: '肉類海鮮',
          img: require('@/assets/images/icon_category_4.png')
        },
        {
          title: '蛋奶豆五穀',
          img: require('@/assets/images/icon_category_5.png')
        },
        {
          title: '調味料',
          img: require('@/assets/images/icon_category_6.png')
        },
        {
          title: '特色農產',
          img: require('@/assets/images/icon_category_7.png')
        },
        {
          title: '食材箱',
          img: require('@/assets/images/icon_category_8.png')
        }
      ]
    };
  },
}
</script>

<style lang="scss" scoped>

.container {
  height: fit-content; 
  display: flex; 
  padding: 0 24px;
  .sidebar-title {
     writing-mode: vertical-lr; 
     padding: 12px; 
     background-color: $green02;
     h4 {
        color: $white; 
        letter-spacing: 4px;
     }
     p {
        color: #ccc; 
        letter-spacing: 2px;
     }
  }
  .navbar-nav {
    flex-direction: row;
    flex-wrap: wrap; 
    padding: 12px 0; 
    width: 100%; 
    background-color: #FBF4E9;
    .nav-item {
      width: calc(100% / 8);
      .nav-link {
        display: flex; 
        justify-content: center; 
        flex-wrap: wrap;
        padding: 0;
        p {
          font-size: 16px; 
          font-weight: bold; 
          color: $black; 
          width: 100%; 
          text-align: center;
        }
      }
      .nav-link:hover {
        p {
          color: $green02;
        }
      }
    }
  }
}

@include lg-width() {
  .container {
    .navbar-nav {
      .nav-item {
        width: calc(100% / 4);
        margin: 6px 0;
      }
    }
  }
}

@include sm-width() {
  .container {
    .navbar-nav {
      .nav-item {
        width: calc(100% / 3);
        margin: 6px 0;
      }
    }
  }
}

@include extra-sm-width() {
  .container {
    .navbar-nav {
      .nav-item {
        width: calc(100% / 2);
        margin: 6px 0;
      }
    }
  }
}

</style>


