<template>
    <div>
        <loading :active.sync="isLoading"></loading>
        <div class="card-set">
            <div class="card-item" v-for="(item, index) in products" :key="item.id" v-show="index < parentNumber && (item.category == category || category == null)">
                <div class="card-content">
                    <router-link :to="`/product/${item.id}`" class="card-image" :style="{'backgroundImage': `url(${item.imageUrl})`}"></router-link>
                    <router-link :to="`/product/${item.id}`"><h6 class="card-title">{{item.title}}</h6></router-link>

                    <div class="card-checkout">
                        <div class="card-price">
                            <div class="h5" v-if="!item.price">${{item.price}}</div>
                            <div class="h5" v-if="item.price">${{item.price}}</div>
                            <del class="h7" v-if="item.price">${{item.origin_price}} </del>
                        </div>
                        <button class="card-button" type="button" @click.prevent="addToCart(item.id)">
                            <p>加入購物車</p><i class="fas fa-shopping-cart"></i>
                        </button>
                    </div>
                </div>
            </div>
        </div>

        <!-- Modal -->
        <div class="modal fade" id="statusModal" tabindex="-1" role="dialog" aria-labelledby="statusModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
            <div class="modal-content">
                <div class="modal-body" style="text-align: center;">
                <i class="fas fa-check-circle fa-5x" style="color: $green02;"></i>
                <p style="text-align: center; font-size: 24px; font-weight: bold;">已加入購物車</p>
                </div>
            </div>
            </div>
        </div>
    </div>
</template>

<script>

import $ from 'jquery';

export default {
  name: 'ProductItem',
  props: ["parentNumber"],
  data() {
    return{
      products: [],
      cart: [],
      category: null,
      isLoading: false,
    };
  },
  methods: {
    getProducts(){
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products/all`;
      const vm = this;
      vm.isLoading = true;
      vm.$http.get(api).then((response) => {
        vm.products = response.data.products; 
        vm.isLoading = false;
      });
    },
    getCart(){
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      const vm = this;
      vm.$http.get(api).then((response) => {
        vm.cart = response.data.data;
      });
    },
    deleteCart(id){
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      const vm = this;
      vm.$http.delete(api).then((response) => {
      });
    },
    addToCart(id, num = 1){
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      const vm = this;
      var cart = {
        product_id: id,
        qty: num
      }

      // 此迴圈是為了將同品項的數量一起計算
      for(var i=0; i < vm.cart.carts.length ; i++){
        if(vm.cart.carts[i].product_id == cart.product_id){
          cart.qty = cart.qty + vm.cart.carts[i].qty;
          vm.deleteCart(vm.cart.carts[i].id);
        }
      }

      vm.isLoading = true;
      vm.$http.post(api, { "data": cart }).then((response) => {
        vm.getCart();
        vm.$bus.$emit('updateCart');
        vm.isLoading = false;
        $('#statusModal').modal('show');
        setTimeout(function(){ $('#statusModal').modal('hide'); }, 3000);
      });
    },
  },
  watch: {
    '$route.query.category'() {
      this.category = this.$route.query.category;
    }
  },
  created() {
    this.getProducts();
    this.getCart();
    this.category = this.$route.query.category;
  },
}
</script>
 
<style lang="scss" scoped>

.card-set {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    .card-item {
        width: calc( 100% / 4);
        .card-content {
            display: flex; 
            justify-content: center; 
            flex-wrap: wrap;
            margin: 20px 10px;
            margin-bottom: 4px;
            padding: 0;
            padding-bottom: 12px;
            border: 1.5px solid $gray02;
            background-color: $white;
            .card-image {
                width: 100%;
                padding-top: 70%;
                background-size: cover;
                background-position: center;
            }
            .card-title {
                text-align: center; 
                width: fit-content;
                height: 22px;
                overflow: hidden;
                margin-top: 12px;
                margin-bottom: 0;
                font-weight: bold;
                color: $black;
            }
            .card-title:hover {
                color: $green02;
            }
            .card-checkout {
                display: flex;
                align-items: center;
                justify-content: center;
                flex-wrap: wrap;
                .card-price {
                    width: 100%;
                    display: flex;
                    justify-content: center;
                    align-items: flex-end;
                    margin-top: 4px;
                    .h5 {
                      color: $black;  
                      font-weight:bold; 
                      margin-bottom: 0;
                    }
                    .h7 {
                      color: #666; 
                      padding-left: 4px;
                    }
                }  
                .card-button {
                  margin-top: 12px; 
                  border: 1.5px solid $green02; 
                  background-color: $green02; 
                  width: fit-content; 
                  padding: 4px 24px; 
                  border-radius: 20px; 
                  display: flex; 
                  justify-content: center; 
                  align-items: center;
                  p {
                    font-size: 16px; 
                    color: $white;
                  }
                  i {
                    color: $white;
                  }
                }
                
            }
        }
    }
}

@include lg-width() {
  .card-set {
    .card-item {
      width: calc(100% / 3);
    }
  }
}

@include md-width() {
  .card-set {
    .card-item {
      width: calc(100% / 2);
    }
  }
}

@include extra-sm-width() {
  .card-set {
    .card-item {
      .card-content {
        .card-checkout {
          .card-button {
            padding: 2px 8px; 
          }
        }
      }
    }
  }
}

</style>


