<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right mt-4">
      <button class="btn btn-primary" @click="openModal(true)">
        建立新的優惠券
      </button>
    </div>

    <!-- Modal Start -->
    <div class="modal fade" id="couponModal" tabindex="-1" role="dialog" aria-labelledby="couponModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="couponModalLabel">
              <span>新增優惠券</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-12">
                <div class="form-row">
                    <div class="form-group col-md-6">
                        <label for="title">標題</label>
                        <input type="text" class="form-control" id="title" placeholder="請輸入標題" v-model="tempCoupon.title"/>
                    </div>
                    <div class="form-group col-md-6">
                        <label for="code">優惠碼</label>
                        <input type="text" class="form-control" id="code" placeholder="請輸入優惠碼" v-model="tempCoupon.code"/>
                    </div>   
                </div>
                

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="percent">折扣百分比</label>
                    <input type="text" class="form-control" id="percent" placeholder="請輸入折扣百分比" v-model="tempCoupon.percent"/>
                  </div>
                  <div class="form-group col-md-6">
                    <label for="due-date">到期日</label>
                    <input type="date" class="form-control" id="due-date" placeholder="請輸入到期日" v-model="tempCoupon.due_date"/>
                  </div>
                </div>

                <div class="form-group">
                  <div class="form-check">
                    <input class="form-check-input" type="checkbox" id="is_enabled" v-model="tempCoupon.is_enabled" :true-value="1" :false-value="0"/>
                    <label class="form-check-label" for="is_enabled">
                      是否啟用
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-primary" @click="updateCoupon">確認</button>
          </div>
        </div>
      </div>
    </div>
    <!-- Modal End -->

    <!-- Modal Start -->
    <div class="modal fade" id="delCouponModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>刪除產品</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            是否刪除 <strong class="text-danger">{{ tempCoupon.title }}</strong> 商品(刪除後將無法恢復)。
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-danger" @click="deleteCoupon">確認刪除</button>
          </div>
        </div>
      </div>
    </div>
    <!-- Modal End -->

    <table class="table mt-4">
      <thead>
        <tr>
          <th>名稱</th>
          <th>優惠碼</th>
          <th style="width:140px;">折扣百分比</th>
          <th style="width:140px;">到期日</th>
          <th style="width:140px;">是否啟用</th>
          <th style="width:140px;">編輯</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in coupons" :key="item.id">
          <td>{{item.title}}</td>
          <td>{{item.code}}</td>
          <td>{{item.percent}}</td>
          <td>{{new Date(item.due_date * 1000).toISOString().split('T')[0]}}</td>
          <td>
            <span v-if="item.is_enabled" class="text-success">已啟用</span>
            <span v-else>未啟用</span>
          </td>
          <td>
              <button class="btn btn-outline-primary btn-sm" @click="openModal(false,item)">編輯</button>
              <button class="btn btn-outline-danger btn-sm" @click="openDeleteModal(item)">刪除</button>
          </td>
        </tr>
      </tbody>
    </table>

    <Pagination :page-data="pagination" v-on:refresh="getCoupons"></Pagination>

  </div>
</template>

<script>
import $ from "jquery";
import Pagination from "../Pagination";

export default {
  components: {
    Pagination,
  },
  data() {
    return {
      coupons: [],
      pagination: {},
      tempCoupon: {},
      inNew: false,
      isLoading: false,
    };
  },
  methods: {
    openModal(isNew, item) {
      const vm = this;
      if (isNew) {
        vm.tempCoupon = {};
        vm.isNew = true;
      } else {
        vm.tempCoupon = Object.assign({}, item);
        vm.tempCoupon.due_date = new Date(vm.tempCoupon.due_date * 1000).toISOString().split('T')[0];
        vm.isNew = false;
      }
      $("#couponModal").modal("show");
    },
    updateCoupon() {
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`;
      let httpMethod = 'post';
      let vm = this;

      vm.tempCoupon.due_date = Math.floor(new Date(vm.tempCoupon.due_date) / 1000);

      if(!vm.isNew){
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
        httpMethod = 'put';
      }

      vm.isLoading = true;
      vm.$http[httpMethod](api, {data: vm.tempCoupon}).then((response) => {
        console.log(response.data);
        $('#couponModal').modal('hide');
        vm.getCoupons();
        vm.isLoading = false;
      });
    },
    getCoupons(page = 1) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupons?page=${page}`;
      const vm = this;
      vm.isLoading = true;

      vm.$http.get(api).then((response) => {
        vm.coupons = response.data.coupons;
        vm.pagination = response.data.pagination;
        vm.isLoading = false;
      });
    },
    openDeleteModal(item){
      const vm = this;
      vm.tempCoupon = Object.assign({},item);
      $('#delCouponModal').modal('show');
    },
    deleteCoupon(){
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
      vm.$http.delete(api).then((response) => {
        $('#delCouponModal').modal('hide');
        vm.getCoupons();
      });
    },
  },
  created() {
    this.getCoupons();
  },
};
</script>

<style lang="scss" scoped>

@include md-width() {
  table {
    display: block; 
    overflow-x: auto; 
    white-space: nowrap;
  }  
}

</style>


