<template>
  <div class="container">
    <div class="step">
      <div class="sidebar-title">
        <h4>步驟</h4>
        <p>Step</p>
      </div>
      <ul class="navbar-nav">
        <li class="nav-item" :class="{active: isActive[0]}">
          <img src="@/assets/images/icon_step_1.png" height="50px" alt="">
          <p>1.確認商品</p>
        </li>
        <li class="nav-item" :class="{active: isActive[1]}">
          <img src="@/assets/images/icon_step_2.png" height="50px" alt="">
          <p>2.填寫資料</p>
        </li>
        <li class="nav-item" :class="{active: isActive[2]}">
          <img src="@/assets/images/icon_step_3.png" height="50px" alt="">
          <p>3.完成訂購</p>
        </li>
      </ul>
    </div>   
  </div>
</template>

<script>

export default {
  props: ['page'],
  data() {
    return {
      isActive: [false,false,false]
    };
  },
  methods: {
    addActive() {
      var vm = this;
      if(vm.page == '/main-cart/main-order'){
        vm.isActive[0] = true;
        vm.isActive[1] = false;
        vm.isActive[2] = false;
      }
      else if(vm.page == '/main-cart/info'){
        vm.isActive[0] = false;
        vm.isActive[1] = true;
        vm.isActive[2] = false;
      }
      else if(vm.page == '/main-cart/checkout'){
        vm.isActive[0] = false;
        vm.isActive[1] = false;
        vm.isActive[2] = true;
      }
    }
  },
  created(){
    this.addActive();
  }
}



</script>

<style lang="scss" scoped>

.container {
  width: 100%;
  .step {
    width: 100%;
    margin-top: -60px; 
    display: flex; 
    justify-content: center; 
    position: relative;
    z-index: 1;
    .sidebar-title {
      writing-mode: vertical-lr; 
      padding: 12px; 
      background-color: $green02;
      h4 {
        color: $white; 
        letter-spacing: 4px;
      }
      p {
        color: #ccc; 
        letter-spacing: 2px;
      }
    }
    .navbar-nav {
      flex-direction: row; 
      flex-wrap: wrap;
      padding: 12px 0; 
      width: fit-content; 
      min-width: 600px;
      background-color: #FBF4E9;
      .nav-item {
        width: calc(100% / 3); 
        display: flex; 
        justify-content: center; 
        flex-wrap: wrap;
        img {
          filter: grayscale(100%);
        }
        p {
          font-size: 16px; 
          font-weight: bold; 
          color: $black; 
          width: 100%; 
          text-align: center; 
          color: #999;
        }
      }
      .active {
        img {
          filter: grayscale(0%);
        }
        p {
          color: $black;
        }
      }
    }
  }  
}

@include md-width() {
  .container {
    .step {
      .navbar-nav {
        min-width: 85%;
      }
    }
  }
}

@include extra-sm-width() {
  .container {
    .step {
      .navbar-nav {
        min-width: 80%;
        .nav-item {
          width: calc(100% / 2);
          margin: 4px 0;
        }
      }
    }
  }
}

</style>


