<template>
  <div class="wrapper">
    <loading :active.sync="isLoading"></loading>
    <div class="banner">
      <div class="slider">
        <div
          :style="{
            backgroundImage:
              'url(' +
              require('@/assets/images/phuc-long-aqrIcYonB-o-unsplash.jpg') +
              ')',
          }"
        ></div>
        <div
          :style="{
            backgroundImage:
              'url(' +
              require('@/assets/images/farmers-g541b3b1b1_1280.jpg') +
              ')',
          }"
        ></div>
        <div
          :style="{
            backgroundImage:
              'url(' +
              require('@/assets/images/markus-spiske-pZX9QPxeIQc-unsplash.jpg') +
              ')',
          }"
        ></div>
      </div>
      <img class="subtitle" src="@/assets/images/subtitle.png" alt="" />
      <div class="news">
        <p>
          <img src="@/assets/images/megaphone_red.png" alt="" height="36" />
          慶開幕購物不限金額打9折，折扣碼NEW90
        </p>
      </div>
    </div>

    <div class="products">
      <div class="container">
        <div class="title">
          <h2>
            <img src="@/assets/images/pop_new.png" alt="" width="60" />
            新品上架
          </h2>
          <router-link to="/product-list"><p>看更多>></p></router-link>
        </div>

        <ProductItem v-bind:parent-number="number"></ProductItem>
      </div>
    </div>

    <div class="container">
      <div class="box">
        <div
          class="box-img"
          :style="{
            backgroundImage:
              'url(' + require('@/assets/images/food/vegetable-box.jpg') + ')',
          }"
        ></div>
        <div class="box-content">
          <h2>食材箱方案</h2>
          <p class="box-text">
            精選當季
            <span>6種蔬菜、2種水果，</span><br />
            均衡營養一次攝取。
          </p>
          <div class="box-price">
            <div class="h5">優惠價：$399</div>
            <del class="h7">$549</del>
          </div>
          <button
            class="box-button"
            type="button"
            @click.prevent="addToCart('-MpKUaTixGr4FHM9UHJh')"
          >
            <p>加入購物車</p>
            <i class="fas fa-shopping-cart"></i>
          </button>
        </div>
      </div>
    </div>

    <MainSidebar class="main-sidebar" style="margin-bottom: 48px"></MainSidebar>

    <div class="slogan">
      <div
        class="sub-banner"
        :style="{
          backgroundImage:
            'url(' +
            require('@/assets/images/farmers-g9c20d3547_1280.jpg') +
            ')',
        }"
      ></div>
      <p>
        我們重視自然環境、永續農業，<br />
        以熱情和誠意行事，<br />
        追求安心、安全、美味的本質。<br />
      </p>
    </div>

    <!-- Modal -->
    <div
      class="modal fade"
      id="statusModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="statusModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-body" style="text-align: center">
            <i class="fas fa-check-circle fa-5x" style="color: $green02"></i>
            <p style="text-align: center; font-size: 24px; font-weight: bold">
              已加入購物車
            </p>
          </div>
        </div>
      </div>
    </div>

    <Footer></Footer>

    <ToTop></ToTop>
  </div>
</template>

<script>
import $ from "jquery";
import MainSidebar from "../MainSidebar";
import ProductItem from "../ProductItem";
import Footer from "../Footer";
import ToTop from "../ToTop";

export default {
  name: "Home",
  components: {
    MainSidebar,
    ProductItem,
    Footer,
    ToTop,
  },
  data() {
    return {
      number: 8,
      cart: [],
      isLoading: false,
    };
  },
  methods: {
    getCart() {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      const vm = this;
      vm.$http.get(api).then((response) => {
        vm.cart = response.data.data;
      });
    },
    deleteCart(id) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      const vm = this;
      vm.$http.delete(api).then((response) => {});
    },
    addToCart(id, num = 1) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      const vm = this;
      var cart = {
        product_id: id,
        qty: num,
      };

      // 此迴圈是為了將同品項的數量一起計算
      for (var i = 0; i < vm.cart.carts.length; i++) {
        if (vm.cart.carts[i].product_id == cart.product_id) {
          cart.qty = cart.qty + vm.cart.carts[i].qty;
          vm.deleteCart(vm.cart.carts[i].id);
        }
      }

      vm.isLoading = true;
      vm.$http.post(api, { data: cart }).then((response) => {
        vm.getCart();
        vm.$bus.$emit("updateCart");
        vm.isLoading = false;
        $("#statusModal").modal("show");
        setTimeout(function () {
          $("#statusModal").modal("hide");
        }, 3000);
      });
    },
  },
  created() {
    this.getCart();
  },
};
</script>

<style lang="scss" scoped>

.wrapper {
  background-color: $gray01;
  .banner {
    width: 100%;
    height: 100vh;
    position: relative;
    overflow: hidden;
    .slider {
      width: 100%;
      height: 100vh;
      div {
        width: 100%;
        height: 100%;
        background-position: center;
        background-size: cover;
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        filter: brightness(70%);
        -webkit-animation: round 12s linear infinite;
        animation: round 12s linear infinite;
        background-attachment: fixed;
        @-webkit-keyframes round {
          0% {
            opacity: 0;
          }
          4% {
            opacity: 1;
          }
          45% {
            transform: scale(1.1);
            opacity: 1;
          }
          49% {
            opacity: 0;
          }
        }
        @keyframes round {
          0% {
            opacity: 0;
          }
          4% {
            opacity: 1;
          }
          45% {
            transform: scale(1.1);
            opacity: 1;
          }
          49% {
            opacity: 0;
          }
        }
      }
      div:nth-child(3) {
        -webkit-animation-delay: 0s;
        animation-delay: 0s;
      }
      div:nth-child(2) {
        -webkit-animation-delay: 4s;
        animation-delay: 4s;
      }
      div:nth-child(1) {
        -webkit-animation-delay: 8s;
        animation-delay: 8s;
      }
    }
    .subtitle {
      width: 600px;
      position: absolute;
      top: 40%;
      left: 48%;
      transform: translate(-48%, -40%);
    }
    .news {
      background-color: rgba(255, 255, 255, 0.8);
      width: 80%;
      height: fit-content;
      filter: brightness(100%);
      padding: 8px;
      border-radius: 40px;
      position: absolute;
      bottom: 8%;
      left: 50%;
      transform: translateX(-50%);
      p {
        font-size: 24px;
        font-weight: bold;
        color: $black;
        text-align: center;
      }
    }
  }
  .products {
    display: flex;
    flex-wrap: wrap;
    padding: 36px 0;
    .title {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      h2 {
        width: fit-content;
      }
      p {
        margin-left: 8px;
        font-weight: bold;
        font-size: 16px;
        color: $green02;
      }
    }
  }
  .box {
    width: 100%;
    height: fit-content;
    display: flex;
    align-items: stretch;
    flex-wrap: wrap;
    padding: 0 10px;
    margin-top: 24px;
    margin-bottom: 36px;
    .box-img {
      width: 60%;
      padding-top: 33%;
      background-size: cover;
      background-position: center;
    }
    .box-content {
      width: 40%;
      padding: 0 10px;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-content: space-between;
      .box-text {
        width: 100%;
        text-align: center;
        padding: 12px 0;
        span {
          color: $green01;
          font-weight: bold;
          font-size: 24px;
        }
      }
    }
    .box-price {
      width: 100%;
      display: flex;
      align-items: flex-end;
      justify-content: center;
      .h5 {
        color: $black;
        font-weight: bold;
        margin-bottom: 0;
      }
      .h7 {
        color: $black;
        padding-left: 4px;
      }
    }
  }
  .slogan {
    position: relative;
    .sub-banner {
      width: 100%;
      height: 350px;
      background-attachment: fixed;
      background-position: center;
      background-size: cover;
      filter: brightness(70%);
    }
    p {
      width: 100%;
      color: $white;
      text-shadow: 0 0 10px $black;
      font-size: 36px;
      text-align: center;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}

h2 {
  font-weight: bold;
  position: relative;
  font-size: 26px;
  padding-left: 60px;
  margin-left: 10px;
  height: fit-content;
  width: 100%;
}
h2::before {
  content: "";
  width: 54px;
  height: 3px;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background-color: $black;
}

p {
  font-size: 18px;
  color: $black;
}

.box-button {
  margin-top: 12px;
  border: 1.5px solid $green02;
  background-color: $green02;
  width: fit-content;
  padding: 4px 24px;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  p {
    font-size: 16px;
    color: $white;
  }
  i {
    color: $white;
  }
}

@include lg-width() {
  .wrapper {
    .banner {
      height: 550px;
    }
  }
}

@include md-width() {
  .wrapper {
    .box {
      .box-img {
        width: 50%;
      }
      .box-content {
        width: 50%;
      }
    }
  }
}

@include sm-width() {
  .wrapper {
    .banner {
      .subtitle {
        width: 400px;
      }
    }
    .box {
        border: 1px solid $gray02;
        padding: 0;
        margin-left: 10px;
        margin-right: 10px;
        width: calc(100% - 20px);
      .box-img {
        width: 100%;
      }
      .box-content {
        width: 100%;
        padding: 16px;
        background-color: $white;
        h2 {
            margin-left: 0;
        }
      }
    }
    .slogan {
      .sub-banner {
        height: 280px;
      }
      p {
        font-size: 26px;
      }
    }
  }
}

@include extra-sm-width() {
  .wrapper {
    .banner {
      .subtitle {
        width: 280px;
      }
    }
    .slogan {
      p {
        font-size: 24px;
      }
    }
  }
}
</style>





