<template>

  <nav aria-label="Page navigation example">
    <ul class="pagination">
      <li class="page-item" :class="{'disabled': !pageData.has_pre}">
        <a class="page-link" href="#" aria-label="Previous" @click.prevent="refreshPage(pageData.current_page - 1)">
          <span aria-hidden="true">&laquo;</span>
          <span class="sr-only">Previous</span>
        </a>
      </li>
      <li class="page-item" :class="{'active': pageData.current_page == page}" v-for="page in pageData.total_pages" :key="page">
        <a class="page-link" href="#" @click.prevent="refreshPage(page)">{{page}}</a>
      </li>
      <li class="page-item" :class="{'disabled': !pageData.has_next}">
        <a class="page-link" href="#" aria-label="Next" @click.prevent="refreshPage(pageData.current_page + 1)">
          <span aria-hidden="true">&raquo;</span>
          <span class="sr-only">Next</span>
        </a>
      </li>
    </ul>
  </nav>
  
</template>

<script>

export default {
  name: 'Pagination',
  props: ['pageData'],
  methods: {
    refreshPage(page){
      this.$emit('refresh', page);
    },
  }
  
}
</script>





