<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    
    <Step :page="path"></Step>
    
    <div style="width: 100%; display: flex; justify-content: center;">
      <div class="my-5" style="width: 680px;">
        <h2 style="width: fit-content; margin-bottom: 24px; margin-left: 0;">
          填寫資料
        </h2>
        <validation-observer v-slot="{ handleSubmit}">
          <form @submit.prevent="handleSubmit(createOrder)">
          
            <validation-provider rules="required" v-slot="{ errors, classes }">
              <div class="form-group">
                <!-- 輸入框 -->
                <label for="username" style="font-size: 18px; font-weight: bold;">姓名</label>
                <input id="name" type="text" name="姓名" v-model="form.user.name"
                    class="form-control" :class="classes">
                <!-- 錯誤訊息 -->
                <span class="invalid-feedback">{{ errors[0] }}</span>
              </div>
            </validation-provider>

            <validation-provider rules="required|numeric" v-slot="{ errors, classes }">
              <div class="form-group">
                <!-- 輸入框 -->
                <label for="usertel" style="font-size: 18px; font-weight: bold;">電話</label>
                <input id="tel" type="tel" name="電話" v-model="form.user.tel"
                    class="form-control" :class="classes">
                <!-- 錯誤訊息 -->
                <span class="invalid-feedback">{{ errors[0] }}</span>
              </div>
            </validation-provider>

            <validation-provider rules="required|email" v-slot="{ errors, classes }">
              <div class="form-group">
                <!-- 輸入框 -->
                <label for="email" style="font-size: 18px; font-weight: bold;">Email</label>
                <input id="email" type="email" name="email" v-model="form.user.email"
                    class="form-control" :class="classes">
                <!-- 錯誤訊息 -->
                <span class="invalid-feedback">{{ errors[0] }}</span>
              </div>
            </validation-provider>

            <validation-provider rules="required" v-slot="{ errors, classes }">
              <div class="form-group">
                <!-- 輸入框 -->
                <label for="useraddress" style="font-size: 18px; font-weight: bold;">收件地址</label>
                <input id="address" type="text" name="收件地址" v-model="form.user.address"
                    class="form-control" :class="classes">
                <!-- 錯誤訊息 -->
                <span class="invalid-feedback">{{ errors[0] }}</span>
              </div>
            </validation-provider>

            <div class="form-group">
              <label for="comment" style="font-size: 18px; font-weight: bold;">留言</label>
              <textarea name="" id="comment" class="form-control" cols="20" rows="5" v-model="form.message"></textarea>
            </div>

            <div class="cart-botton" style="display: flex; justify-content: space-between;">
              <router-link to="/main-cart/main-order"><button class="btn" style="background-color: #406343; color: #fff; padding: 8px 36px;">上一步</button></router-link>
              <button class="btn" type="submit" style="background-color: #406343; color: #fff; padding: 8px 36px;">送出訂單</button>
            </div>
          </form>
        </validation-observer>
      </div>
    </div>
    
  </div>
        
</template>

<script>

import Step from '../Step';

export default {
  data() {
    return{
      isLoading: false,
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: ''
        },
        message: ''
      },
      path: ''
    }
  },
  components: {
    Step
  },
  methods: {
    createOrder(){
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
      const vm = this;
      const order = vm.form;
      vm.isLoading = true;
      vm.$http.post(api, {data: order}).then((response) => {
        if(response.data.success){
          vm.isLoading = false;
          vm.$bus.$emit('updateCart');
          vm.$router.push(`/main-cart/checkout/${response.data.orderId}`)
        }
      });
    },
  },
  created() {
    this.path = this.$route.path;
  },
}

</script>



