<template>
    <button class="top-btn" @click="toTop">
        <i class="fas fa-chevron-up fa-2x"></i>
    </button>
</template>

<script>

export default {
  name: 'ToTop',
  methods: {
      toTop() {
          document.documentElement.scrollIntoView({behavior: "smooth"});
      }
  }
}
</script>

<style lang="scss" scoped>

.top-btn {
    position: fixed; 
    right: 10px; 
    bottom: 10px; 
    background-color: rgba(221,221,221,0.9); 
    width: 50px;
    height: 50px; 
    border-radius: 50%;
    z-index: 10;
    i {
        color: #666; 
        position: absolute; 
        top: 50%; 
        left: 50%; 
        transform: translate(-50%,-50%);
    }
}

@include lg-width() {
    .top-btn {
        bottom: 90px;
    }
}

</style>




