<template>

  <div class="wrapper">
    <div class="banner"></div>
    <div class="container">
      <div class="login" style="margin: 48px 0; width: 100%; display: flex; justify-content: center; flex-wrap: wrap;">
        
        <div class="login-form" style="width: 400px; background-color: #eaeaea; padding: 36px; border-radius: 20px;">
          <h1 style="text-align: center; width: 100%;">登入</h1>
          <form class="validate-form" @submit.prevent="signin" style="margin-top: 36px; display: flex; justify-content: center; flex-wrap: wrap;">

              <div class="validate-input" data-validate="Please enter email: ex@abc.xyz" style="width: 100%;">
                <div class="form-group">
                  <label for="email" style="font-size: 18px; font-weight: bold;">E-mail</label>
                  <input type="text" name="email" v-model="user.username" class="form-control">
                </div>
              </div>

              <div class="validate-input" data-validate = "Please enter password" style="width: 100%;">
                <div class="form-group">
                  <label for="pass" style="font-size: 18px; font-weight: bold;">密碼</label>
                  <input type="password" name="pass" v-model="user.password" class="form-control">  
                </div>
              </div>

              <button class="box-button">
                  <p>送出</p>
              </button>  
              
            </form>  
        </div>
        
      </div>
      
    </div>
    <Footer></Footer>
  </div>
  
</template>

<script>

import Footer from '../Footer';

export default {
  name: 'Login',
  components: {
    Footer,
  },
  data () {
    return {
      user:{
        username: '',
        password: ''
      }
    }
  },
  methods: {
    signin() {
      const api = `${process.env.APIPATH}/admin/signin`;
      const vm = this;
      this.$http.post(api,vm.user).then((response) => {
        if(response.data.success){
          // const token = response.data.token;
          // const expired = response.data.expired;
          // document.cookie = `hexToken=${token}; expires=${new Date(expired)}`;
          const { token, expired } = response.data;
          document.cookie = `hexToken=${token}; expires=${expired}`;
          vm.$router.push('/admin/products');
        }
      });
    }
  },
}
</script>

<style lang="scss" scoped>

html, body {
  background-color: $gray01;
}

h2 {
    font-weight: bold; 
    position: relative; 
    font-size: 26px; 
    padding-left: 60px ;
    margin-left: 10px;
    height: fit-content;
    width: 100%;
}
h2::before {
    content: '';
    width: 54px;
    height: 3px;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    background-color: $black;
}

.box-button {
    margin-top: 12px; 
    border: 1.5px solid $green02; 
    background-color: $green02; 
    width: fit-content; 
    padding: 4px 24px; 
    border-radius: 20px; 
    display: flex; 
    justify-content: center; 
    align-items: center;
    p {
    font-size: 16px; 
    color: $white;
    }
    i {
    color: $white;
    }
}

.wrapper {
  width: 100%;
  background-color: $gray01;
  .banner {
    width: 100%;
    height: 70px;
    background-color: $green02;
  }
}
  
</style>



