<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    
    <Step :page="path"></Step>
  
    
    <div class="my-5">
      <h2>
        訂單明細
      </h2>
      <form @submit.prevent="payOrder">
          <table class="product-table table">
          <thead>
              <th>產品</th>
              <th width="120">數量</th>
              <th width="80">價格</th>
          </thead>
          <tbody>
              <tr v-for="item in order.products" :key="item.id">
              <td class="align-middle product-title">
                <div :style="{'backgroundImage': `url(${item.product.imageUrl})`}"></div>
                <p>{{ item.product.title }}</p>
              </td>
              <td class="align-middle" style="text-align: center; font-size: 16px; font-weight: bold;">{{ item.qty }}</td>
              <td class="align-middle" style="text-align: center; font-size: 16px; font-weight: bold;">{{ item.final_total }}</td>
              </tr>
          </tbody>
          <tfoot>
              <tr>
              <td colspan="2" class="text-right" style="font-size: 18px; font-weight: bold;">總計：</td>
              <td class="text-right" style="font-size: 18px; font-weight: bold; color: #6ecb63;">${{ order.total }}</td>
              </tr>
          </tfoot>
          </table>


          <h2 style="width: fit-content; margin-bottom: 24px; margin-left: 0;">
            收件資料
          </h2>
          <table class="table">
          <tbody>
              <tr>
              <th width="130" style="font-size: 16px; font-weight: bold;">Email</th>
              <td style="font-size: 16px;">{{ order.user.email }}</td>
              </tr>
              <tr>
              <th style="font-size: 16px; font-weight: bold;">姓名</th>
              <td style="font-size: 16px;">{{ order.user.name }}</td>
              </tr>
              <tr>
              <th style="font-size: 16px; font-weight: bold;">收件人電話</th>
              <td style="font-size: 16px;">{{ order.user.tel }}</td>
              </tr>
              <tr>
              <th style="font-size: 16px; font-weight: bold;">收件人地址</th>
              <td style="font-size: 16px;;">{{ order.user.address }}</td>
              </tr>
              <tr>
              <th style="font-size: 16px; font-weight: bold;">付款狀態</th>
              <td>
                  <span style="font-size: 16px; font-weight: bold;" v-if="!order.is_paid" class="text-danger">尚未付款</span>
                  <span style="font-size: 16px; font-weight: bold;" v-else class="text-success">付款完成</span>
              </td>
              </tr>
          </tbody>
          </table>
          <!-- <div class="text-right" >
          <button class="btn btn-danger">確認付款去</button>
          </div> -->
          <div class="cart-botton" style="display: flex; justify-content: space-between;">
            <router-link to="/product-list"><button class="btn" style="background-color: #406343; color: #fff; padding: 8px 36px;">繼續購物</button></router-link>
            <button class="btn" type="submit" v-if="order.is_paid === false" style="background-color: #406343; color: #fff; padding: 8px 36px;">確認付款</button>
          </div>
      </form>
    </div> 
  </div>
        
</template>

<script>

import Step from '../Step';

export default {
  data() {
    return{
      orderId: '',
      order: {
          user: {

          }
      },
      path: ''
    }
  },
  components: {
    Step
  },
  methods: {
    getOrder(){
        const vm = this;
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order/${this.orderId}`;
        vm.isLoading = true;
        vm.$http.get(api).then((response) => {
            vm.order = response.data.order;
            vm.isLoading = false;
        });
    },
    payOrder(){
        const vm = this;
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/pay/${this.orderId}`;
        vm.isLoading = true;
        vm.$http.post(api,vm.orderId).then((response) => {
            vm.getOrder();
            vm.isLoading = false;
        });
    },
  },
  created() {
    this.orderId = this.$route.params.orderId;
    this.path = this.$route.path.substring(0,19);
    console.log(this.path);
    this.getOrder();
  },
}

</script>

<style lang="scss">

.product-table {
  width: 100%;
  thead {
    th {
      text-align: center;
      font-size: 16px;
    }
  }
  tbody {
    tr {
      .product-title {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        div {
          width: 100px;
          height: 100px;
          margin-right: 12px;
          background-position: center;
          background-size: cover;
        }
        p {
          text-align: center;
          font-size: 16px;
          font-weight: bold;
        }
      }
    }
  }
}

h2 {
  font-weight: bold;
  position: relative;
  font-size: 26px;
  padding-left: 60px;
  height: fit-content;
  width: fit-content;
  margin-bottom: 24px;
  margin-left: 0;
}
h2::before {
    content: '';
    width: 54px;
    height: 3px;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    background-color: $black;
}



@include sm-width() {
  .table {
    tbody {
      tr {
        .product-title {
          justify-content: center;
        }
      }
    }
  }
}

@include extra-sm-width() {
  .table {
    tbody {
      tr {
        .product-title {
          div {
            width: 60px;
            height: 60px;
            margin-right: 0;
            background-position: center;
            background-size: cover;
          }
          p {
            word-break: break-all;
          }
        }
      }
    }
  }
}

</style>


