<template>
    <div class="quality">
        <button class="operator" @click.prevent="changeQuantity('-', parentId)">-</button>
        <input type="number" value="1" class="qty" v-model="currentQty">
        <button class="operator" @click.prevent="changeQuantity('+', parentId)">+</button>    
    </div>
    
</template>

<script>

export default {
  name: "Quality",
  props: ['parentQty','parentId'],
  data() {
    return {
      currentQty: 1,
    };
  },
  methods: {
    changeQuantity(operator, id=null) {
      const vm = this;
      if (operator == "+") {
        vm.currentQty = parseInt(vm.currentQty) + 1;
      } else if (operator == "-" && vm.currentQty >= 2) {
        vm.currentQty = parseInt(vm.currentQty) - 1;
      }
      this.$emit('qty',id, vm.currentQty);
    },
  },
  created(){
      this.currentQty = this.parentQty;
  }
};
</script>

<style lang="scss">

.quality {
    display: flex;
    justify-content: center;
    .operator {
        background-color: $gray02;
        border: 3px solid $gray02;
        width: 40px;
    }
    .qty {
        text-align: center;
        font-size: 16px;
        width: 80px;
        border-top: 3px solid $gray02;
        border-bottom: 3px solid $gray02;
    }    
}

@include sm-width() {
  .quality {
    display: flex;
    .operator {
        width: 30px;
    }
    .qty {
        width: 50px;
    }    
  }
}

@include extra-sm-width() {
  .quality {
    display: flex;
    .operator {
        width: 20px;
    }
    .qty {
        width: 40px;
    }    
  }
}


</style>




