<template>
  <div class="wrapper">
    <loading :active.sync="isLoading"></loading>
    <div class="banner"></div>
    <div class="container">
      <div class="product">
        <div
          class="product-img"
          :style="{ backgroundImage: `url(${product.imageUrl})` }"
        ></div>
        <div class="product-content">
          <p class="product-breadcrumb">
            <a href="/">首頁</a> / <router-link to="/product-list">所有產品</router-link> / <router-link :to="{ path: '/product-list', query: { category: product.category }}">{{ product.category }}</router-link>
          </p>
          <h1 class="product-title">{{ product.title }}</h1>
          <p class="product-description">{{ product.description }}</p>
          <div class="product-price">
            <div class="h3" v-if="!product.price">
              ${{ product.price }}
            </div>
            <div class="h3" v-if="product.price">
              ${{ product.price }}
            </div>
            <del class="h7" v-if="product.price">
              ${{ product.origin_price }}
            </del>
          </div>
          <form class="d-flex mb-4">
            <Quality v-on:qty="countQty" :parent-qty="1"></Quality>
            <button type="submit" class="product-cart btn btn-primary text-nowrap" @click.prevent="addToCart(productId,currentQty)">
              加入購物車<i class="fas fa-shopping-cart"></i>
            </button>
          </form>
        </div>
      </div>
      <div class="notice">
        <h4
          style="
            font-weight: bold;
            border-bottom: 1.5px solid #406343;
            padding-bottom: 4px;
          "
        >
          訂購須知
        </h4>
        <div class="notice-content">
          <div class="h5" style="font-weight:bold;">付款方式與限制：</div>
          <p style="font-size: 18px;">
            ※目前付款方式僅提供信用卡與銀行轉帳。<br />
            <br />
          </p>
          <div class="h5" style="font-weight:bold;">配送方式與須知：</div>
          <p style="font-size: 18px;">
            ※配送方式：黑貓宅急便宅配到府。<br />
            ※配送限制：為確保送達您手中的產品保持最佳品質，不同溫層產品將分開配送。<br />
            <br />
          </p>
        </div>
      </div>
    </div>

    <Footer></Footer>
    <ToTop></ToTop>

    <!-- Modal -->
    <div class="modal fade" id="statusModal" tabindex="-1" role="dialog" aria-labelledby="statusModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-body" style="text-align: center;">
            <i class="fas fa-check-circle fa-5x" style="color: $green02;"></i>
            <p style="text-align: center; font-size: 24px; font-weight: bold;">已加入購物車</p>
          </div>
        </div>
      </div>
    </div>

    

  </div>
</template>


<script>

import $ from 'jquery';
import Footer from '../Footer';
import Quality from '../Quality';
import ToTop from '../ToTop';

export default {
  name: "Product",
  components: {
    Footer,
    Quality,
    ToTop
  },
  data() {
    return {
      productId: "",
      product: {},
      cart: [],
      currentQty: 1,
      category: "",
      isLoading: false,
    };
  },
  methods: {
    getProduct(id) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
      const vm = this;
      vm.isLoading = true;
      vm.$http.get(api).then((response) => {
        vm.product = response.data.product;
        vm.isLoading = false;
      });
    },
    getCart(){
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      const vm = this;
      vm.$http.get(api).then((response) => {
        vm.cart = response.data.data;
      });
    },
    deleteCart(id){
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      const vm = this;
      vm.$http.delete(api).then((response) => {
      });
    },
    addToCart(id, num = 1){
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      const vm = this;
      var cart = {
        product_id: id,
        qty: num
      }

      // 此迴圈是為了將同品項的數量一起計算
      for(var i=0; i < vm.cart.carts.length ; i++){
        if(vm.cart.carts[i].product_id == cart.product_id){
          cart.qty = cart.qty + vm.cart.carts[i].qty;
          vm.deleteCart(vm.cart.carts[i].id);
        }
      }

      vm.isLoading = true;
      vm.$http.post(api, { "data": cart }).then((response) => {
        vm.getCart();
        vm.$bus.$emit('updateCart');
        vm.isLoading = false;
        $('#statusModal').modal('show');
        setTimeout(function(){ $('#statusModal').modal('hide'); }, 3000);
      });
    },
    countQty(newId,newQty){
      this.currentQty = newQty;
    }
  },
  created() {
    this.productId = this.$route.params.productId;
    this.getProduct(this.productId);
    this.getCart();
    
  },
};
</script>


<style lang="scss" scoped>

.wrapper {
  width: 100%;
  .banner {
    width: 100%;
    height: 70px;
    background-color: $green02;
  }
  .container {
    .product {
      width: 100%;
      padding-top: 36px;
      padding-bottom: 48px;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      .product-img {
        width: 400px;
        height: 400px;
        background-position: center;
        background-size: cover;
      }
      .product-content {
        width: calc(100% - 400px);
        padding-left: 32px;
        .product-breadcrumb {
          margin-bottom: 24px;
        }
        .product-title {
          margin-bottom: 20px;
        }
        .product-description {
          font-size: 18px;
          margin-bottom: 48px;
        }
        .product-price {
          display: flex;
          align-items: flex-end;
          margin-bottom: 20px;
          .h3 {
            color: $green01; 
            font-weight: bold; 
            margin-bottom: 0;
          }
          .h7 {
            color: #666; 
            padding-left: 4px;
          }
        }
        form {
          background-color: #fbf4e9; 
          padding: 24px;
          flex-wrap: wrap;
          .product-cart {
            margin-left: 12px;
            background-color: $green02;
            border: 0px solid $black;
          }
        }
      }
    }
    .notice {
      margin-bottom: 36px;
      .notice-content {
        padding: 24px;
        background-color: $gray01;
      }
    }
  }
}

@include lg-width() {
  .wrapper {
    .container {
        .product {
          .product-img {
            width: 300px;
            height: 300px;
          }
          .product-content {
            width: calc(100% - 300px);
          }
        }
    } 
  }
}

@include md-width() {
  .wrapper {
    .container {
        .product {
          .product-img {
            width: 200px;
            height: 200px;
          }
          .product-content {
            width: calc(100% - 200px);
            form {
              .product-cart {
                margin-left: 0;
                margin-top: 12px;
              }
            }
          }
        }
    } 
  }
}

@include sm-width() {
  .wrapper {
    .container {
        .product {
          .product-img {
            width: 240px;
            padding-top: 240px;
          }
          .product-content {
            width: 100%;
            padding-left: 0;
            margin-top: 24px;
            form {
              .product-cart {
                margin-left: 0;
                margin-top: 12px;
              }
            }
          }
        }
    } 
  }
}


</style>


