<template>
  <div>
    <loading :active.sync="isLoading"></loading>

    <Step :page="path"></Step>

    <div v-if="!hasCart">
      <div class="h1">尚未選購任何商品</div>
    </div>
    <div class="my-5" v-else>
      <h2>確認商品</h2>
      <div>
        <table class="table">
          <thead>
            <th>產品</th>
            <th width="120">數量</th>
            <th width="120">價格</th>
            <th width="40"></th>
          </thead>
          <tbody>
            <tr v-for="item in cart.carts" :key="item.id">
              <td class="product-title align-middle">
                <div
                  :style="{ backgroundImage: `url(${item.product.imageUrl})` }"
                ></div>
                <p>{{ item.product.title }}</p>
              </td>
              <td class="align-middle">
                <Quality
                  v-on:qty="addToCart"
                  :parent-qty="item.qty"
                  :parent-id="item.product_id"
                ></Quality>
              </td>
              <td class="align-middle text-right">
                <div
                  style="text-align: center; font-size: 16px; font-weight: bold"
                >
                  ${{ item.final_total }}
                </div>
                <del class="text-success" v-if="item.coupon"
                  >${{ item.total }}</del
                >
              </td>
              <td class="align-middle text-center">
                <button
                  type="button"
                  class="btn btn-sm"
                  @click="deleteCart(item.id)"
                >
                  <i class="fas fa-times"></i>
                </button>
              </td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td
                colspan="3"
                class="text-right"
                style="font-size: 18px; font-weight: bold"
              >
                總計：
              </td>
              <td
                class="text-right"
                style="font-size: 18px; font-weight: bold; color: #6ecb63;"
              >
                ${{ cart.total }}
              </td>
            </tr>
            <tr v-if="cart.final_total != cart.total">
              <td colspan="3" class="text-right" style="font-size: 18px; font-weight: bold">折扣價</td>
              <td class="text-right" style="font-size: 18px; font-weight: bold; color: #6ecb63;">${{ cart.final_total }}</td>
            </tr>
          </tfoot>
        </table>
        <div class="input-group mb-3 input-group-sm">
          <input
            type="text"
            v-model="coupon_code"
            class="form-control"
            placeholder="請輸入優惠碼"
          />
          <div class="input-group-append">
            <button
              class="btn btn-outline-secondary"
              type="button"
              @click="addCoupon"
            >
              套用優惠碼
            </button>
          </div>
        </div>
      </div>

      <div
        class="cart-botton"
        style="display: flex; justify-content: space-between"
      >
        <button
          class="btn"
          style="background-color: #406343; color: #fff; padding: 8px 36px"
        >
          繼續購物
        </button>
        <router-link to="/main-cart/info"
          ><button
            class="btn"
            style="background-color: #406343; color: #fff; padding: 8px 36px"
          >
            下一步
          </button></router-link
        >
      </div>
    </div>
  </div>
</template>

<script>
import Quality from "../Quality";
import Step from "../Step";

export default {
  data() {
    return {
      cart: [],
      coupon_code: "",
      currentQty: 1,
      isLoading: false,
      path: "",
      hasCart: false,
    };
  },
  components: {
    Quality,
    Step,
  },
  methods: {
    getCart() {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      const vm = this;
      vm.isLoading = true;
      vm.$http.get(api).then((response) => {
        vm.cart = response.data.data;
        vm.isLoading = false;

        if (vm.cart.carts.length != 0) {
          vm.hasCart = true;
        } else {
          vm.hasCart = false;
        }

        vm.cart.carts.sort(function (x, y) {
          let a = x.product_id;
          let b = y.product_id;
          if (a > b) return 1;
          if (a < b) return -1;
          else return 0;
        });
      });
    },
    deleteCart(id) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      const vm = this;
      vm.isLoading = true;
      vm.$http.delete(api).then((response) => {
        vm.$bus.$emit("updateCart");
        vm.getCart();
        vm.isLoading = false;
      });
    },
    addToCart(id, num = 1) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      const vm = this;
      var cart = {
        product_id: id,
        qty: num,
      };

      // 此迴圈是為了將同品項的數量一起計算
      for (var i = 0; i < vm.cart.carts.length; i++) {
        if (vm.cart.carts[i].product_id == cart.product_id) {
          vm.deleteCart(vm.cart.carts[i].id);
        }
      }

      vm.isLoading = true;
      vm.$http.post(api, { data: cart }).then((response) => {
        vm.getCart();
        vm.$bus.$emit("updateCart");
        vm.isLoading = false;
      });
    },
    addCoupon() {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
      const vm = this;
      const coupon = {
        code: vm.coupon_code,
      };
      vm.isLoading = true;
      vm.$http.post(api, { data: coupon }).then((response) => {
        console.log(response);
        vm.getCart();
        vm.isLoading = false;
      });
    },
  },
  created() {
    this.getCart();
    this.path = this.$route.path;
  },
};
</script>

<style lang="scss" scoped>

.h1 {
  margin: 96px 0;
  text-align: center;
}

.table {
  width: 100%;
  thead {
    th {
      text-align: center;
      font-size: 16px;
    }
  }
  tbody {
    tr {
      .product-title {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        div {
          width: 100px;
          height: 100px;
          margin-right: 12px;
          background-position: center;
          background-size: cover;
        }
        p {
          text-align: center;
          font-size: 16px;
          font-weight: bold;
        }
      }
    }
  }
}

h2 {
  font-weight: bold;
  position: relative;
  font-size: 26px;
  padding-left: 60px;
  height: fit-content;
  width: fit-content;
  margin-bottom: 24px;
  margin-left: 0;
}
h2::before {
  content: "";
  width: 54px;
  height: 3px;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background-color: $black;
}

@include sm-width() {
  .table {
    tbody {
      tr {
        .product-title {
          justify-content: center;
        }
      }
    }
  }
}

@include extra-sm-width() {
  .table {
    tbody {
      tr {
        .product-title {
          div {
            width: 60px;
            height: 60px;
            margin-right: 0;
            background-position: center;
            background-size: cover;
          }
          p {
            word-break: break-all;
          }
        }
      }
    }
  }
}
</style>


