<template>
    <nav class="navbar navbar-expand-lg header-bg" id="navbar" style="height: 70px;">
        <div class="container">
            <a class="navbar-brand" href="/" style="background-color: transparent; box-shadow: inset 0 0 0 rgb(0 0 0 / 25%);">
                <img src="@/assets/images/logo.svg" height="32" alt="">
            </a>
            <div class="collapse navbar-collapse justify-content-end" id="navbarNav">
                <ul class="navbar-nav">
                    <li class="nav-item active">
                        <a href="/" class="nav-link">首頁</a>
                    </li>
                    <li class="nav-item">
                        <router-link class="nav-link" to="/product-list">產品</router-link>
                    </li>
                    <li class="nav-item">
                        <router-link class="nav-link" to="/login">
                            <i class="fas fa-user"></i>
                        </router-link>
                    </li>
                    <li class="nav-item">
                        <router-link class="nav-link" to="/main-cart/main-order">
                            <i class="fas fa-shopping-cart">
                                <div class="cart_qty">
                                    <p>{{qty}}</p>
                                </div>    
                            </i>
                        </router-link>
                    </li>
                </ul>
            </div>

            <div class="navbar-mobile">
                <div class="container">
                    <ul>
                        <li class="nav-item active">
                            <a href="/" class="nav-link">
                                <i class="fas fa-shopping-cart"></i>
                                <p>首頁</p>
                            </a>
                        </li>
                        <li class="nav-item">
                            <router-link class="nav-link" to="/product-list">
                                <i class="fas fa-shopping-cart"></i>
                                <p>產品</p>
                            </router-link>
                        </li>
                        <li class="nav-item">
                            <router-link class="nav-link" to="/login">
                                <i class="fas fa-user"></i>
                                <p>會員</p>
                            </router-link>
                        </li>
                        <li class="nav-item">
                            <router-link class="nav-link" to="/main-cart/main-order">                                
                                <i class="fas fa-shopping-cart">
                                    <div class="cart_qty">
                                        <p>{{qty}}</p>
                                    </div>    
                                </i>
                                <p>購物車</p>
                                
                            </router-link>
                        </li>
                    </ul>    
                </div>
                
            </div>
        </div>
    </nav>
</template>

<script>

export default {
  data() {
    return{
      cart: [],
      qty: 0,
      scroll: window.scrollY
    };
  },
  methods: {
    getCart(){
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      const vm = this;
      vm.$http.get(api).then((response) => {
        vm.cart = response.data.data.carts;
        vm.qty = 0;
        for( var i=0 ; i < vm.cart.length; i++){
            vm.qty += vm.cart[i].qty;
        }
      });
    },
    scrollNav(){
        this.scroll = window.scrollY;
        var element = document.getElementById("navbar");
        if(this.scroll == 0){
            element.classList.add("header-bg");
        }
        else {
            element.classList.remove("header-bg");
        }
    }
  },
  created() {
    const vm = this;
    vm.getCart();
    this.$bus.$on('updateCart', function() {
        vm.getCart();
    });
  },
  mounted(){
    window.addEventListener("scroll", this.scrollNav)
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.scrollNav);
  }
}
</script>

<style lang="scss" scoped>

.navbar {
    position: fixed;
    width: 100%;
    height: 50px;
    background-color: $green02;
    transition: 0.3s;
    z-index: 10;
    .nav-item {
        .nav-link {
            color: $white;
            font-size: 18px;
            i {
                position: relative;
                .cart_qty{
                    position: absolute; 
                    top: -10px; 
                    right: -14px; 
                    background-color: #FF5151; 
                    width: 22px; 
                    height: 20px; 
                    border-radius: 50%;
                    z-index: 1;
                    p {
                        position: absolute; 
                        top: 50%; 
                        left: 50%; 
                        transform: translate(-50%, -50%); 
                        font-size: 10px;
                        color: $white;
                    }
                }
            }
            
        }    
    }
    .navbar-mobile {
        display: none;
        width: 100%; 
        position: fixed; 
        left:0; 
        bottom: 0; 
        background-color: #FBF4E9; 
        border-top: 1px solid $green02;
        ul {
            width: 100%; 
            display: flex; 
            justify-content: center;
            li {
                width: calc(100% / 4); 
                margin: 6px; 
                margin-top: 16px;
                .nav-link {
                    display: flex; 
                    flex-wrap: wrap; 
                    justify-content: center; 
                    padding: 0;
                    i {
                        font-size: 24px; 
                        color: $green02;
                    }
                    p {
                        font-size: 18px; 
                        color: $green02; 
                        width: 100%; 
                        text-align: center;
                    }
                }
            }
        }
    }
}
.header-bg {
    background-color: transparent;
    transition: 0.3s;
    .nav-item {
        .nav-link {
            color: $white;
        }
    }
}

@include lg-width() {
    .navbar {
        .navbar-mobile {
            display: flex;
        }
    }
}
</style>




