<template>
    <div class="wrapper">
        <loading :active.sync="isLoading"></loading>
        <div class="banner">
          <div :style="{backgroundImage: 'url(' + require('@/assets/images/ja-ma-gOUx23DNks-unsplash.jpg') + ')' }">
          </div>
          <h1>產品總覽</h1>  
        </div>
        <MainSidebar class="main-sidebar"></MainSidebar> 
        <div class="container">
             
            <div class="content">
              <h2 v-if="category != null">{{category}}</h2>
              <h2 v-else>所有產品</h2>
              <ProductItem v-bind:parent-number = "number"></ProductItem>
            </div>
        </div>
        <Footer></Footer>
        <ToTop></ToTop>
    </div>
</template>

<script>

import MainSidebar from '../MainSidebar';
import ProductItem from '../ProductItem';
import Footer from '../Footer';
import ToTop from '../ToTop';

export default {
  name: 'ProductList',
  components: {
    MainSidebar,
    ProductItem,
    Footer,
    ToTop
  },
  data() {
    return{
      products: [],
      number: 0,
      category: null,
      isLoading: false,
    };
  },
  methods: {
    getProducts(){
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products/all`;
      const vm = this;
      vm.isLoading = true;
      vm.$http.get(api).then((response) => {
        vm.products = response.data.products;
        vm.number = vm.products.length;
        vm.isLoading = false;
      });
    },
  },
  created() {
    this.getProducts();
    this.category = this.$route.query.category;
  },
  watch: {
    '$route.query.category'() {
      this.category = this.$route.query.category;
    }
  },
}
</script>

<style lang="scss" scoped>

h2 {
    font-weight: bold; 
    position: relative; 
    font-size: 26px; 
    padding-left: 60px ;
    margin-left: 10px;
    height: fit-content;
    width: 100%;
}
h2::before {
    content: '';
    width: 54px;
    height: 3px;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    background-color: $black;
}

.wrapper {
    width: 100%;
    background-color: $gray01;
    .banner {
        width: 100%;
        height: 65vh;
        position: relative;
        div {
          width: 100%;
          height: 100%;
          background-position: center;
          background-size: cover;
          filter: brightness(70%);
        }
        h1 {
          width: 100%;
          text-align: center;
          text-shadow: 0 0 10px $black; 
          color: $white; 
          position: absolute; 
          top: 50%; 
          left: 50%; 
          transform: translate(-50%,-50%);
        }
    }
    .main-sidebar {
      width: 100%;
      margin-top: -60px;
      position: relative;
      z-index: 1;
    }
    .container {
        .content {
            width: 100%;
            margin: 48px 0;
        }    
    }
    
}

</style>





