<template>
    <div class="wrapper">
        <div class="banner">
          <div :style="{backgroundImage: 'url(' + require('@/assets/images/markus-spiske-wL7pwimB78Q-unsplash.jpg') + ')' }">
          </div>
          <h1>購物清單</h1>  
        </div>
        <div class="container">
          <router-view></router-view>
        </div>
        <Footer></Footer>
    </div>
</template>

<script>

import Footer from './Footer';

export default {
  name: "MainCart",
  components: {
    Footer
  },
};
</script>

<style lang="scss">
.wrapper {
  width: 100%;
  .banner {
    position: relative;
    width: 100%;
    height: 65vh;
    div {
      width: 100%;
      height: 100%;
      background-position: center;
      background-size: cover;
      filter: brightness(70%);
    }
    h1 {
      width: 100%;
      text-align: center;
      text-shadow: 0 0 10px $black; 
      color: $white; 
      position: absolute; 
      top: 50%; 
      left: 50%; 
      transform: translate(-50%,-50%);
    }
  }
}
    
</style>


